.categoryCard6 {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: nowrap;
    flex-shrink: 0;
    gap: 16px;
    position: relative;
    width: 624px;
    padding: 0 0 24px 0;
    background: #ffffff;
    border: 1px solid #d8e2e6;
    z-index: 10;
  }
  .rectangle7 {
    flex-shrink: 0;
    position: relative;
    width: 624px;
    height: 426px;
    background: url("../../../../images/Rectangle-4.jpg")
      no-repeat center;
    background-size: cover;
    z-index: 11;
  }
  .dining {
    flex-shrink: 0;
    flex-basis: auto;
    position: relative;
    height: 32px;
    color: #0c1012;
    font-family: Adelle, var(--default-font-family);
    font-size: 24px;
    font-weight: 600;
    line-height: 32px;
    text-align: left;
    white-space: nowrap;
    z-index: 12;
  }